
.home-page {
  min-height: calc(100vh - 60px);
  background: url('@/assets/images/bg-home.jpg') 50% 0/cover no-repeat;
  padding: 16px;
  display: flex;
  flex-direction: column;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0 16px;
  }

  &__title {
    font-weight: 800;
    font-size: 18px;
    line-height: 120%;
    margin: 0;
  }
}

.slider-arrows {
  display: flex;
  align-items: center;
  gap: 4px;

  &__arrow {
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: #fff;
    filter: drop-shadow(0px 4px 15px rgba(22, 25, 31, 0.1));
    cursor: pointer;

    &.swiper-button-disabled {
      opacity: 0.5;
      cursor: default;
      pointer-events: none;
    }

    &.right {
      img {
        transform: rotate(180deg);
      }
    }
  }
}

.modal {

  p,
  ul {
    margin: 0 0 8px;
  }
}

.swiper {
  flex-grow: 1;
  margin: 0;
  margin-bottom: 16px;
}

.hubber-block {
  background: #FFFFFF;
  border-radius: 8px;
  padding: 8px 16px;
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 12px;
  line-height: 120%;
  display: flex;

  &__logo {
    width: 56px;
    margin-right: 10px;
    flex-shrink: 0;
    align-self: flex-start;
  }
}

.menu {
  margin: 0 -8px;
  padding: 8px 14px;
  list-style: none;
  display: flex;
  background: #fff;
  box-shadow: 0px 4px 15px rgba(22, 25, 31, 0.1);
  border-radius: 60px;

  &__item {
    padding: 10px;
    width: 50%;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:not(:first-child) {
      border-left: 1px solid #E5EAEF;
    }
  }

  &__ico {
    margin-right: 4px;
  }
}
