
.modal {
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(52, 52, 66, 0.7);
  padding-top: 60px;

  &__dialog {
    max-width: 500px;
    margin: 0 auto;
    padding: 16px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    max-height: calc(100vh - 60px);
  }

  &__header {
    text-align: center;
    transform: translateX(0);
    padding-bottom: 16px;
  }

  &__logo {
    display: block;
    margin: 0 auto;
  }

  &__title {
    margin: 16px 0 0;
    font-size: 18px;
    line-height: 21px;
  }

  &__content {
    background-color: #fefefe;
    padding: 16px 24px;
    border-radius: 16px;
    box-shadow: 0px 4px 15px rgba(22, 25, 31, 0.1);
    overflow: auto;
  }

  &__footer {
    text-align: center;
    padding-top: 24px;
  }
}
